import React, { useEffect, useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { Layout, Menu, Button, theme } from "antd";
import { useTonWallet, useTonAddress } from '@tonconnect/ui-react';
import { TonConnectButton } from '@tonconnect/ui-react';
import { truncateString } from "../../ultils";
import { Modal } from "antd";
import toast from "react-hot-toast";

const { Sider, Content } = Layout;

const getIsMobile = () => window.innerWidth <= 1280;

const TemplateLayout = ({ children }) => {

  const wallet = useTonWallet();
  const walletAddress = useTonAddress();

  const [isMobile, setIsMobile] = useState(getIsMobile());

  const [collapsed, setCollapsed] = useState(isMobile ? true : false);

  const [isShowMenu, setIsShowMenu] = useState(false);


  const [isOpenProfile, setIsOpenProfile] = useState(false);
  const handleCancelProfile = () => {
    setIsOpenProfile(false);
};

  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  const location = useLocation();

  const [activeMenuKey, setActiveMenuKey] = useState(location?.pathname);

  
  const [tonBalance, setTonBalance] = useState(0);

  useEffect(() => {
    setActiveMenuKey(location?.pathname);
  }, [location]);

  useEffect(() => {
    
  }, [wallet, location]);




  useEffect(() => {
    const onResize = () => {
      setIsMobile(getIsMobile());
    };

    window.addEventListener("resize", onResize);

    return () => {
      window.removeEventListener("resize", onResize);
    };
  }, []);

  useEffect(() => {
    if (isMobile) {
      setCollapsed(false);
    }
  }, [isMobile]);

  return (
    <Layout className="relative min-h-screen overflow-hidden game-layout">
      <Sider
        width={56}
        trigger={null}
        collapsible
        collapsed={collapsed}
      >
        <Menu
          className="menu-main"
          theme=""
          mode="inline"
          defaultSelectedKeys={["/"]}
          selectedKeys={[activeMenuKey]}
        >
          <Menu.Item key="/" className="menu-home">
            <NavLink 
              to="/" 
              className="flex flex-col items-center gap-2"
            >
              <span className="max-w-40 ml-0"></span>
            </NavLink>
          </Menu.Item>
          <Menu.Item key="/badge" className="menu-badge">
            <NavLink 
              to="/badge" 
              className="flex flex-col items-center gap-2"
            >
              <span className="max-w-40 ml-0"></span>
            </NavLink>
          </Menu.Item>
          <Menu.Item key="/task" className="menu-task">
            <NavLink 
              to="/task" 
              className="flex flex-col items-center gap-2"
            >
              <span className="max-w-40 ml-0"></span>
            </NavLink>
          </Menu.Item>
          <Menu.Item key="/leaderboard" className="menu-leaderboard">
            <NavLink 
              to="/leaderboard" 
              className="flex flex-col items-center gap-2"
            >
              <span className="max-w-40 ml-0"></span>
            </NavLink>
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout>
        <div className="subhead">
          <div className="ton-connect-button">
            {!walletAddress ? (
              <TonConnectButton />
            ) : (
              <>
                <button
                  className="connected"
                  onClick={() => {
                    setIsOpenProfile(true);
                  }}
                >
                  @
                  {truncateString(walletAddress, 4, 3)}
                </button>
              </>
            )}
          </div>
        </div>
        <Content >
          

          <div className="container mx-auto max-w-[400px]">
            {children}
          </div>

          <div className="game-slots">
            <div className="item">
              <div className="inner">
                <div className="link"></div>
              </div>
            </div>
            <div className="item">
              <div className="inner">
                <div className="link"></div>
              </div>
            </div>
            <div className="item">
              <div className="inner">
                <div className="link"></div>
              </div>
            </div>
            <div className="item">
              <div className="inner">
                <div className="link"></div>
              </div>
            </div>
          </div>
        </Content>

        {walletAddress && (
          <Modal
            forceRender
            open={isOpenProfile}
            onCancel={handleCancelProfile}
            footer={false}
            className="modal-profile"
        >
            <div className="text-center mt-6">
              <div className="address">
                {truncateString( walletAddress, 16, 8 )}
                <button
                  onClick={() => {
                    navigator.clipboard.writeText(walletAddress);
                    toast.success( `Copied: ${truncateString( walletAddress, 16, 5 )}` );
                  }}
                  className="copy"
                ></button>
              </div>
            </div>
            <div className="btn-group flex justify-center gap-1 absolute  bottom-[44px] left-0 right-0">
              <div
                className="btn-common"
                onClick={() => {
                  setIsOpenProfile(false);
                }}
              >
                Close
              </div>
              <div
                className="btn-common grey"
                onClick={() => {
                  // disconnect();
                  // // removeCookie('SolanaSignedIn');
                  // Cookies.remove("token");
                  // window.location.reload();
                }}
              >
                Disconnect
              </div>
            </div>
        </Modal>
        )}
      </Layout>
    </Layout>
  );
};
export default TemplateLayout;
